.SignUp-body {
  background-color: #141414;
  min-height: 118vh;
  display: flex;
  align-items: center;
  justify-content: center;
}

.SignUp-header {
  background-color: #141414;
  display: flex;
  align-items: center;
  justify-content: center;
  font-family: "Jitter";
  padding: 1%;
  font-size: 500%;
  color: white;
}

.SignUp-popup {
  font-family: "Inter-medium";
  background-color: #1b1c1d;
  padding: 2rem;
  border-radius: 12px;
  box-shadow: 0px 8px 16px rgba(0, 0, 0, 0.3);
  color: white;
  transition: all 0.3s ease-in-out;
}

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(-10px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.SignUp-popup {
  animation: fadeIn 0.3s ease-in-out;
}

.Form-Labels {
  padding-top: 2%;
}

/* Center button and style it */
.Submit-Button {
  width: 100%;
  padding: 0.8rem;
  background-color: #578493;
  border: none;
  color: white;
  font-size: 1rem;
  border-radius: 8px;
  transition: background 0.3s ease-in-out;
}

.Submit-Button:hover {
  background-color: #274b78;
}

@font-face {
  font-family: "Jitter";
  src: url("../../Jitter.otf") format("opentype");
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: "Inter-Medium";
  src: url("../../Inter/Inter-Medium.ttf") format("truetype");
  font-weight: normal;
  font-style: normal;
}
